  export const ratio = (numerator: number, denominator: number, multiplier: number = 1): number => {
    // numerator() : denominator() = multiplier : x
    // x = multiplier * denominator / numerator
    return  multiplier * (denominator / numerator)
  // a:b = c:x   x  
    // ax = bc
    // x = bc/a
  }
